<template>
  <div>
    <div class="d-block header-section">
      <h1>
        <b>Internal Stock Requests</b>
      </h1>
      <b-col class="mb-2 col-md-6 float-right search-section">
        <b-form-group
          label
          label-cols-sm="3"
          label-align-sm="right"
          label-size="sm"
          label-for="filterInput"
          class="mb-0"
        >
          <b-input-group>
            <b-form-input
              v-model="filter"
              class="form-control"
              type="search"
              id="filterInput"
              placeholder="Type to Search"
            ></b-form-input>
            <b-input-group-append>
              <b-button :disabled="!filter" @click="filter = ''">
                <img src="@/assets/images/icons/cross.png" alt />
              </b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
      </b-col>
    </div>
    <div class="container-fluid order-list">
      <!-- START: Card Data-->
      <div class="row">
        <div class="col-12 col-md-12 mt-3 mb-5">
          <div class="card pb-3">
            <div class="p-3 list-filter">
              <div class="btn-group">
                <button
                  type="button"
                  class="btn btn-outline-dark rounded-btn mr-2 mob-mar" :class="{'active' : this.Alltab}" @click="onStatusFilterClicked('All')"
                >{{this.AllText}}</button>
              </div>
              <div class="btn-group">
                <input type="checkbox" id="requested" name="stockRequestslist" hidden>
                <label for="requested" type="button" class="btn btn-outline-info rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('1')">{{this.RequestedText}}</label>                
              </div>
              <div class="btn-group">
                <input type="checkbox" id="approved" name="stockRequestslist" hidden>
                <label for="approved" type="button" class="btn btn-outline-success rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('2')">{{this.ApprovedText}}</label>
              </div>              
              <div class="btn-group">
                <input type="checkbox" id="transit" name="stockRequestslist" hidden>
                <label for="transit" type="button" class="btn btn-outline-danger rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('3')">{{this.TransitText}}</label>                
              </div>            
              <div class="btn-group">
                <input type="checkbox" id="closed" name="stockRequestslist" hidden>
                <label for="closed" type="button" class="btn btn-outline-warning rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('4')">{{this.ClosedText}}</label>                
              </div>

              <div class="btn-group">
                <input type="checkbox" id="expired" name="stockRequestslist" hidden>
                <label for="expired" type="button" class="btn btn-outline-warning rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('5')">{{this.ExpiredText}}</label>                
              </div>
            </div>
            <div class="overflow-auto">
              <b-table
                id="stockRequestslist"
                :items="this.getStockRequests"
                :filter="filter"
                filter-debounce="1000"
                :fields="fields"
                :current-page="currentPage"
                :per-page="perPage"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                sort-icon-left
                class="table order"
              >
                <template v-slot:cell(action)="data">
                    <router-link v-if="isViewOnlyRole"
                    :to="{ name: 'StockRequestDetail', params: {requestId: data.item.stockRequestId} }"
                  >View</router-link>   
                  <router-link
                    v-else-if="data.item.status == 'Requested' && userLocationId==data.item.requestingFacilityId"
                    :to="{ name: 'StockRequestDetail', params: {requestId: data.item.stockRequestId} }"
                  >View</router-link>
                  <router-link
                    v-else-if="data.item.status == 'Requested' && userLocationId!=data.item.requestingFacilityId"
                    :to="{ name: 'StockRequestReview', params: {requestId: data.item.stockRequestId} }"
                  >Review</router-link>
                  <router-link
                    v-else-if="data.item.status == 'Approved' && userLocationId==data.item.approvingFacilityId"
                    :to="{ name: 'StockRequestFulfill', params: {requestId: data.item.stockRequestId} }"
                  >Fulfill</router-link>
                  <router-link
                    v-else
                    :to="{ name: 'StockRequestDetail', params: {requestId: data.item.stockRequestId} }"
                  >View</router-link>
                </template>
              </b-table>

              <b-pagination
                v-model="currentPage"
                :total-rows="this.totalRows"
                :per-page="perPage"
                class="float-right mr-4"
                first-text="First"
                prev-text="Prev"
                next-text="Next"
                last-text="Last"
              ></b-pagination>
            </div>
          </div>
          <!-- START: Card Data-->
        </div>
      </div>
      <!-- END: Card DATA-->
    </div>
  </div>
</template>

<script>
import axios from "axios";
import roles from '../../router/roles';
export default {
    data() {
        return {
        userLocationId: null,
        /*Search */
        filter: "",
        /*Filters*/
        requestStatus: [],
        requestsFrom: null,
        requestsTill: null,
        locations: [],
        /*Paginations */
        perPage: 10,
        currentPage: 1,
        totalRows: 0,
        sortBy: "requestedOn",
        sortDesc: true,
        AllText: "",
        RequestedText: "",
        ApprovedText: "",
        TransitText: "",
        ClosedText: "",
        ExpiredText:"",
        Alltab: true,
        role: "",
        roles:[],
        fields:[
            {
            key: "number",
            label:"Stock Request Number"
            },
            {
            key: "requestedStamps",
            sortable: true,
            },
            {
            key: "fulfilledStamps"            
            },
            {
            key: "requestedOn",
            sortable: true,
            },
            {
            key: "status",
            },
            {
            key: "requestingFacility",
            sortable: true
            },
            {
            key: "approvingFacility"
            },
            {
            key: "action",
            label: "Actions",
            },
        ],        
        };
    },    
    created() {
    this.role=this.$store.state.clientAppDecodedToken.Role
    this.userLocationId = this.$store.state.clientAppDecodedToken.LocationId;
    this.roles= roles;
  },
    components: {},
    computed: {
      isViewOnlyRole: function(){
        return this.role == roles.TpsafAdmin || this.role == roles.TsspWarehouseIncharge || this.role == roles.TsspAdmin || this.role == roles.TsspIntermediate;
      }
    },
    methods: {  
        onStatusFilterClicked(status){
        if(status=="All")
        {
            this.requestStatus=[];
            this.$root.$emit('bv::refresh::table', 'stockRequestslist');
            this.Alltab = true;
            var items=document.getElementsByName('orderlist');
                    for(var i=0; i<items.length; i++){
                        if(items[i].type=='checkbox')
                            items[i].checked=false;
                    }
            return;
        }
        var statusInt = parseInt(status)
        const index = this.requestStatus.indexOf(statusInt);
        if (index > -1) {
            this.requestStatus.splice(index, 1);
            this.Alltab = false;
        }
        else{
            this.requestStatus.push(statusInt);
            this.Alltab = false;
        }
        
        //force refresh on status change
        this.$root.$emit('bv::refresh::table', 'stockRequestslist');
        },	
        getStockRequests: function (ctx) {
            const promise = axios({
                url: "/InternalStock/GetStockRequests",
                method: "post",
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                data: {
                    searchText: ctx.filter,
                    locations: this.locations,
                    statuses: this.requestStatus,
                    requestsFrom: this.requestsFrom,
                    requestsTill: this.requestsTill,
                    sortBy: ctx.sortBy,
                    sortByDesc: ctx.sortDesc,
                    pageNo: ctx.currentPage,
                    pageSize: ctx.perPage
                }
            });

            // Must return a promise that resolves to an array of items
            return promise.then((response) => {
                // Pluck the array of items off our axios response
                const items = response.data.miniStockRequestDtos;
                this.totalRows = response.data.totalCount;
                this.AllText = "All(" + response.data.totalCount + ")";
                this.RequestedText = "Requested(" + response.data.requestedCount + ")";
                this.ApprovedText = "Approved(" + response.data.approvedCount + ")";
                this.TransitText = "Transit(" + response.data.transitCount + ")";
                this.ExpiredText = "Expired(" + response.data.expiredCount + ")";
                this.ClosedText =
                "Closed(" + response.data.closedCount + ")";
                // Must return an array of items or an empty array if an error occurred
                return items || [];
            });
        },
    },    
};
</script>
